img{width:100%;height:100%;background-color:white}.gameOver{background-color:red}.remove{display:none}.apply-shake{animation:shake 0.5s cubic-bezier(0.36, 0.07, 0.19, 0.97) both}@keyframes shake{10%,90%{transform:translate3d(-1px, 0, 0)}20%,80%{transform:translate3d(2px, 0, 0)}30%,50%,70%{transform:translate3d(-4px, 0, 0)}40%,60%{transform:translate3d(4px, 0, 0)}}.main-container{display:block;margin:auto;width:800px}.menu-container{display:flex;justify-content:space-evenly;width:600px;height:100px;margin:auto}.controller{display:flex;justify-content:space-between;align-items:center;flex-direction:column}.label{display:flex;flex-wrap:wrap}.start-container{align-self:center}.start{background-color:red;border-radius:8px;border:0;display:inline-block;cursor:pointer;color:#ffffff;font-size:18px;padding:8px 25px}.start:hover{background-color:#29cf1a}.timer{display:flex;align-items:center;flex-direction:column}.arrow{width:0;height:0;border:0 solid transparent}.arrow:hover{cursor:pointer}.up{border-right-width:14px;border-left-width:13px;border-bottom:18px;border-bottom-style:solid;border-bottom-color:teal}.up-limit{border-bottom-color:grey}.down{border-left-width:14px;border-right-width:13px;border-top:18px;border-top-style:solid;border-top-color:teal}.down-limit{border-top-color:grey}.blink{animation:blinker 2.5s linear infinite}@keyframes blinker{50%{opacity:0.3}}.field-container{display:block;margin:auto;width:max-content;background-image:url(img/bg2.9f2b8.png);margin-top:10px}.row{display:flex;justify-content:center}.cells{width:100px;height:100px;margin:5px;border:0;padding:0;background-image:url(img/question.d4d88.jpg);background-size:cover}.cells:hover{filter:contrast(110%);background-color:white}
